@import "../../SCSS_VERIABLES.scss";

button {
   min-width: 100px;
   max-width: 250px;
   width: auto;
   padding: 0 10px;
   min-height: 40px;
   border: none;
   color: $colorPrimary;
   background-color: $buttonColor;
   font-size: 18px;
   outline: none;
   cursor: pointer;
   letter-spacing: 0.05em;
   font-family: inherit;
}
button:hover{
   box-shadow:  0 0 2px 0 rgba($colorPrimary, .4);
}
button:active{
   box-shadow: inset 0 0 3px 0 rgba($colorPrimary, .6);
}


